
export default class Action {
	constructor(dispatcher, id) {
		this.dispatcher = dispatcher;
		this.id = id;
	}

	/**
	 * .
	 * @param nodeVarname varname
	 * @param isInput false.
	 * @param name 
	 * @param pos [x, y]
	 */
	changePlugPosition(nodeVarname, isInput, name, pos) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "changePlugPosition",
			nodeVarname : nodeVarname,
			isInput : isInput,
			name : name,
			pos : pos
		});
	}

	/**
	 * 
	 * @param id id
	 * @param inputPos 
	 * @param outputPos 
	 */
	dragPlug(plugInfo, inputPos, outputPos) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "dragPlug",
			plugInfo : plugInfo,
			inputPos : inputPos,
			outputPos : outputPos
		});
	}

	/**
	 * 
	 */
	endDragPlug(plugInfo, inputPos, outputPos) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "endDragPlug",
			plugInfo : plugInfo,
			inputPos : inputPos,
			outputPos : outputPos
		});
	}

	/**
	 * 
	 */
	selectPlugHole(plugInfo) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "selectPlugHole",
			plugInfo : plugInfo
		});
	}

	/**
	 * 
	 */
	unSelectPlugHoles() {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "unSelectPlugHoles"
		});
	}

	/**
	 * 
	 */
	disconnectPlugHole(plugInfo) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "disconnectPlugHole",
			plugInfo : plugInfo
		});
	}

	/**
	 * .
	 */
	changeZoom(zoom) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "changeZoom",
			zoom: zoom
		});
	}
}



