import Dispatcher from "./Dispatcher.jsx";

export default class Action {
	constructor(id) {
		this.dispatcher = Dispatcher;
		this.id = id;
	}

    /**
	 * Console
	 * @param show 
	 */
	showConsoleOutput(show) {
        this.dispatcher.dispatch({
			id :this.id,
			actionType: "showConsoleOutput",
			show : show
		});
    }

    /**
	 * Layout
	 * @param mode 
	 */
    setLayout(mode) {
        this.dispatcher.dispatch({
			id :this.id,
			actionType: "setLayout",
			mode : mode
		});
    }

	/**
	 * 
	 * @param nodeInfo 
	 */
	addNode(nodeInfo) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "addNode",
			nodeInfo : nodeInfo
		});
	}

    /**
	 * 
	 * @param nodeInfo 
	 * @param varname varnamenull
	 */
	addNodeByName(nodeName, varname) {
		if (varname) {
			this.addNode({name : nodeName, varname : varname});
		} else {
			this.addNode({name : nodeName});
		}
	}

    /**
	 * SceneScript
	 * @param varname 
	 */
	exportSceneScript(varname) {
        if (!varname) {
            varname = '';
        }
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "exportSceneScript",
			varname : varname
		});
	}

    /**
	 * 
	 * @param varname 
	 */
	exportGroupNode(varname) {
        if (!varname) {
            varname = '';
        }
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "exportGroupNode",
			varname : varname
		});
	}

    /**
	 * 
	 * @param varname 
	 */
	importGroupNode(data) {
        this.dispatcher.dispatch({
			id :this.id,
			actionType: "importGroupNode",
            data: data
		});
	}

	/**
	 * 
	 * @param varname 
	 */
	deleteNode(varname) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "deleteNode",
			varname : varname
		});
	}

	/**
	 * 
	 * @param varname 
	 */
	deleteNodes(varnameList) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "deleteNodes",
			varnameList : varnameList
		});
	}

	/**
	 * .
	 */
	publishInput(inputData) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "publishInput",
			data : inputData
		});
	}

	/**
	 * .
	 */
	unPublishInput(inputData) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "unPublishInput",
			data : inputData
		});
	}

	/**
	 * .
	 */
	publishOutput(outputData) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "publishOutput",
			data : outputData
		});
	}

	/**
	 * .
	 */
	unPublishOutput(outputData) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "unPublishOutput",
			data : outputData
		});
	}

	/**
	 * 
	 */
	changeNode(nodeInfo) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "changeNode",
			nodeInfo : nodeInfo
		});
	}

	/**
	 * 
	 * @param inputInfo 
	 * {
	 *    varname : varname,
	 *    input : {
	 *         1: 1,
	 *         2: 2, ..
	 *    }
	 * }
	 */
	changeNodeInput(inputInfo) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "changeNodeInput",
			inputInfo : inputInfo
		});
	}

	/**
	 * 
	 */
	changeNodes(nodeInfoList) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "changeNodes",
			nodeInfoList : nodeInfoList
		});
	}

	/**
	 * .
	 */
	copy() {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "copy"
		});
	}

	/**
	 * .
	 */
	paste() {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "paste"
		});
	}

	/**
	 * 
	 */
	align() {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "align"
		});
	}

	/**
	 * 
	 */
	delete() {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "delete"
		});
	}

	/**
	 * (
	 */
	clearAll() {
		this.dispatcher.dispatch({
			id : this.id,
			actionType : "clearAll"
		});
	}

	/**
	 * 
	 */
	makeGroup() {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "makeGroup"
		});
	}

	/**
	 * 
	 */
	unGroup() {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "unGroup"
		});
	}

	/**
	 * 
	 */
	digGroup(groupVarname) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "digGroup",
			groupVarname : groupVarname
		});
	}

	/**
	 * 
	 */
	save() {
		this.dispatcher.dispatch({
			id : this.id,
			actionType : "save"
		});
	}

	/**
	 * 
	 * @param data .
	 */
	load(data) {
		this.dispatcher.dispatch({
			id : this.id,
			actionType : "load",
			data : data
		});
	}

	/**
	 * .
	 */
	pasteNodes(nodeInfoList) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "pasteNodes",
			nodeInfoList : nodeInfoList
		});
	}

	/**
	 * 
	 * @param zoom 
	 * @param rect left,top,right,bottom
	 * @param realRect rect
	 * @param nodeSizes {nodevarname : [width, height], ..} 
	 */
	alignNodes(zoom, rect, realRect, nodeSizes) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "alignNodes",
			zoom : zoom,
			rect :rect,
			realRect : realRect,
			nodeSizes : nodeSizes
		});
	};

	/**
	 * 
	 * @param nodeInfo 
	 */
	importNode(nodeInfo) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "importNode",
			nodeInfo : nodeInfo
		});
	}

	/**
	 * 
	 * @param plugInfo 
	 * 
	 * {
	 *    output : {
	 *        nodeVarname : 
	 *        name : 
	 *    }
	 *    input : {
	 *        nodeVarname : 
	 *        name : 
	 *    }
	 * }
	 */
	addPlug(plugInfo) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "addPlug",
			plugInfo : plugInfo
		});
	}

	/**
	 * 
	 * @param plugInfo 
	 */
	deletePlug(plugInfo) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "deletePlug",
			plugInfo : plugInfo
		});
	}

	/**
	 * 
	 * @param varname 
	 * @param isVisible
	 */
	changePanelVisible(varname, isVisible) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "changePanelVisible",
			varname : varname,
			visible : isVisible
		});
	}

	/**
	 * 
	 */
	selectNode(nodeVarnameList) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "selectNode",
			nodeVarnameList : nodeVarnameList
		});
	}

	/**
	 * 
	 * @parma nodeVarnameList varname.
	 * @param excludeVarname 
	 */
	unSelectNode(nodeVarnameList, excludeVarname) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "unSelectNode",
			nodeVarnameList : nodeVarnameList,
			excludeVarname : excludeVarname
		});
	}

	/**
	 * .
	 * @parma keyFrame .
	 */
	changeFrame(frame) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "changeFrame",
			frame : frame
		});
	}

	/**
	 * 
	 * @param frame 
	 * @param node 
	 * @param input 
	 */
	addKeyFrame(frame, node, input) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "addKeyFrame",
			frame : frame,
			node : node,
			input : input
		});
	}

	/**
	 * 
	 * @param frame 
	 * @param node 
	 * @param input 
	 */
	deleteKeyFrame(frame, node, input) {
		this.dispatcher.dispatch({
			id :this.id,
			actionType: "deleteKeyFrame",
			frame : frame,
			node : node,
			input : input
		});
	}
}



